//checkout

.checkout-title{
    margin: 0;
    padding: 0;
    font-size: 0;
    line-height: 0;
}

.arrow {
    transform: rotate(0deg); //預設箭頭向下
}

.upsideDown {
    transform: rotate(180deg); //箭頭向上
}

.text-block {
    display: block; //預設
}


.checkout .footer {
    border-top: 1px solid #00000060;
}

.order {
    background: #F2F2F2;

    .card-header {
        background: #F2F2F2;
        border-bottom: none;
        padding: 1rem 1rem;
    }

    .card-body {
        padding: 1rem 1rem;
    }

    .btn {
        padding: 0rem;
    }




    .btn-link:hover {
        color: $craftsman-primary;

    }

    .btn-link:focus {
        text-decoration: none;
        -webkit-box-shadow: none;
        box-shadow: none;
    }

    .table-img {
        width: 48px;
        height: 48px;
    }
}

.order-table {
    // line-height: 36px;
    color: $craftsman-dark;
    margin-bottom: 0;

    td {
        padding: 0 0 8px 0;
        vertical-align: middle;
        border: none;

        @media (min-width:768px) {
            padding: 0 0 16px 0;
        }
    }

    thead {
        td:first-child {
            width: 64px;
        }
    }

    tbody {
        border-top: 1px solid rgba(0, 0, 0, 0.38);
        border-bottom: 1px solid rgba(0, 0, 0, 0.38);

        tr:nth-child(1) {
            td {
                padding-top: 16px;
            }
        }

        tr:nth-child(2) {
            td {
                padding-bottom: 16px;
            }
        }

        @media (min-width:768px) {
            tr:nth-child(1) {
                td {
                    padding-top: 16px;
                }
            }
        }
    }

    tfoot {
        font-size: 24px;

        td {
            padding-top: 16px;
            padding-bottom: 0;
        }
    }
}


//checkout-info


.info-form {

    .form-group {
        margin-bottom: 0.5rem;
    }

    .form-control {
        // padding: 18px 16px 19px 16px;
        padding: 16px;
        height: calc(1em + 2.4rem + 2px);
        border: 1px solid #00000060;

    }

    //checkbox客製顏色
    .custom-control-label {
        padding-left: 4px;
        padding-top: 4px;
    }

    .custom-control-label::before {
        width: 1.5rem;
        height: 1.5rem;
        content: "";

    }

    .custom-control-label::after {
        width: 1.5rem;
        height: 1.5rem;
        content: "";

    }

    .custom-checkbox .custom-control-input:checked~.custom-control-label::before {
        background-color: $craftsman-primary;
    }

    //select客製下拉箭頭
    .select-arrow {
        background-image: url(../images/arrow_drop_down-black-18dp.svg);
        background-repeat: no-repeat;
        background-size: contain;
        background-position-x: right;
        appearance: none;
        -moz-appearance: none;
        -webkit-appearance: none;
        -ms-appearance: none;
        line-height: 19px;
    }
}

.info-btn-group {
    .btn {
        padding: 19px 0 18px 0;
    }
}



//checkout-payment


.payment-form {

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none; //主要消除inputtype=number的預設箭頭
        margin: 0;
    }

    .form-group {
        margin-bottom: 0.5rem;
    }

    .form-control {
        // padding: 18px 16px 19px 16px;
        padding: 16px;
        height: calc(1em + 2.4rem + 2px);
        border: 1px solid #00000060;

    }
}

.confirmation {
    .btn {
        padding: 0;
    }

    .card-header {
        background: #FFFFFF00 0% 0% no-repeat padding-box;
        border-bottom: 0;
        padding: 1rem 1rem;
    }

    .card-body {
        padding: 0 1rem 1rem 1rem;
        line-height: 21px;
    }

}

//checkout-welldnne
.success {
    .title {
        a {
            color: #fff;
            font-family: Raleway;
            font-weight: 900;
            font-size: 24px;

            @media (min-width:768px) {
                color: $craftsman-dark;
            }
        }
    }

    .slogan {
        padding-top: 212px;
        padding-bottom: 24px;
        // color: #FFFFFF99;

        @media (min-width:768px) {
            padding-top: 215px;
            padding-bottom: 268px;
            // color: $craftsman-dark;

            .shop-btn {
                margin-top: 167px;
            }
        }


    }
}

