

.product-nav {
    background: #F2F2F2;
}

.product-list {
    margin-bottom: 0;
    color: #0000009A;

}

.product-list li {
    margin-bottom: 0;
    font-size: 24px;
    line-height: 28px;
    position: relative;

    @media (max-width:767px) {
        font-size: 16px;
    }

    &:hover {
        color: $craftsman-primary;
        cursor: pointer;

        .badge {
            color: $craftsman-primary;
        }
    }

    &.active {
        color: $craftsman-primary;

        .badge {
            color: $craftsman-primary;
        }
    }
}

.product-list li .badge {
    padding: 0.3rem 0;
    color: #0000009A;
    position: absolute;
    top: 0px;
    @media (min-width:767px) {
        top: -5px;
    }
}

.pageNum-link {
    font-size: 24px;
    color: $craftsman-dark;

    &:hover {
        color: $craftsman-primary;
        font-weight: bold;
    }
    &.active {
        color: $craftsman-primary;
        font-weight: bold;
    }
}

.product-content .card:nth-child(1) img,
.product-content .card:nth-child(5) img,
.product-content .card:nth-child(9) img{
    height: 350px;
    @media (max-width:767px) {
        height: 161px;
    }
}
.product-content .card:nth-child(2) img,
.product-content .card:nth-child(6) img,
.product-content .card:nth-child(7) img {
    height: 240px;

    @media (max-width:767px) {
        height: 161px;
    }
}
.product-content .card:nth-child(3) img,
.product-content .card:nth-child(4) img,
.product-content .card:nth-child(8) img {
    height: 480px;

    @media (max-width:767px) {
        height: 161px;
    }
}

@media only screen and (max-width: 768px) {
    .card-columns {
        column-count: 2 !important;
    }

    .card-columns .card img {
        max-height: 200px;
        object-fit: cover;
    }
}
