.shopping-card {
    del {
        color: #00000062;
    }

    .card-text {
        @media (min-width:768px) {
            width: 100%;

        }
    }

    .card-link {
        width: 100%;

        @media (min-width:768px) {
            width: 64%;

        }
    }
}

.num-control {
    width: 50%;

    .num-control-btn {
        background: #F2F2F2;
        font-size: 24px;
        padding: 1rem 1rem;
    }

    .num-control-input {
        background: #F2F2F2;
        padding: 1rem 1rem;
    }

    .num-control-input:focus {
        outline: 1px solid #F2F2F2;
        background: white;
    }

    @media (min-width:768px) {
        width: 34%;

        .num-control-btn {
            padding: 0.375rem 0.75rem;
        }

        .num-control-input {

            padding: 0.375rem 0.75rem;
        }
    }
}