



.navbar-dark .navbar-toggler {
    color: #fff;
    border-color: rgba(255, 255, 255, 0.1);
}


.navbar-title {
    font-size: 24px;
    line-height: 29px;
    padding: 0;

}

.shop-btn {
    width: 100%;
    margin-top: 108px;
    padding: 1rem 0;
    @media (min-width:767px) {
        padding: 16px 45px;
        width: auto;
        margin-top: 0;
    }
}
.banner-img{
    top: 0;
    bottom: 0;
    background-image: url(../images/tom-crew-5HRCkrSs9Dk-unsplash.jpg);
    z-index: -1;
    height: 640px;
    @media (min-width:767px) {
        height: 800px;
    }
}


.banner{
    height: 578px;
    @media (min-width:767px) {
        height: 723px;
    }
    .slogan{
        // height: 518px;
        padding-top: 128px;
        padding-bottom: 24px;
        text-align: center;
        @media (min-width:767px) {
            padding-top: 159px;
            padding-bottom: 236px;
            text-align: left;
        }
    }
}

