//晚點移去base
.h7 {
    font-size: 14px;
    line-height: 16px;
}

.bg-cover {
    background-size: cover;
    background-position: center center;

}

.font-raleway {
    font-family: Raleway;
}

.font-weight-900 {
    font-weight: 900;
}

.navbar-title {
    font-size: 24px;
    line-height: 29px;
    padding: 0;

}
.text-md-dark {

    @media (min-width:768px) {
        color: $craftsman-dark !important;
    }
}

@media (min-width: 768px) {
    .navbar-md-light .navbar-toggler {
        color: rgba(0, 0, 0, 0.5);
        border-color: rgba(0, 0, 0, 0.1);
    }

}

.rounded-top-0 {
    border-top-left-radius: 0rem;
    border-top-right-radius: 0rem;

}

.rounded-bottom-0 {
    border-bottom-right-radius: 0rem;
    border-bottom-left-radius: 0rem;
}