

//offcanvas
.open .wrap {

    transition: 1s ease;
}

.open .aside {
    transform: translateX(0px);
}


.aside {
    top: 0;
    bottom: 0;
    // min-width: 360px;
    width: 100%;
    height: 100%;
    overflow: hidden;
    // transform: translateX(-360px);
    transform: translateX(-100%);
    transition: all 1s;
    z-index: 1;
}

.aside .card-body {
    padding: 1rem 0;

    a {
        text-decoration: none;
        color: #0000009A;

        &:hover {
            color: $craftsman-primary;
        }
    }

}

.aside {

    .btn {
        font-size: 24px;
        padding: 1rem 0;
    }

    .btn-link {
        color: $craftsman-dark;
        text-decoration: none;
        border-bottom: 1px solid #00000062;

        &:hover {
            text-decoration: none;
            background: #F2F2F2;
            color: $craftsman-primary;
        }

    }
}

.navbar-light .navbar-nav .nav-link {
    color: $craftsman-dark;
}

//網路上找到的翻轉icon寫法
[data-toggle="collapse"][aria-expanded="true"]>.js-rotate-if-collapsed {
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    transform: rotate(180deg);
}



//subscription
.subscription {
    background-image: url(../images/tom-crew-uDF1mW7EUwM-unsplash.jpg);
    background-repeat: no-repeat;
}

.subscription-title {
    font-size: 48px;
    line-height: 64px;

    @media (max-width:767px) {
        font-size: 32px;
        line-height: 1.5;
    }
}

.subscription-input {
    background-color: transparent;
    border: 1px solid #FFFFFF99;
    border-radius: 4px 0px 0px 4px;

}

