//footer
.footer-title {
    font-weight: 900;
    font-family: Raleway;
    font-style: italic;
}

.copyright {
    color: $white-100;
}

.policy {
    color: $white-200;
}

//modal
.modal.right .modal-dialog {
    position: fixed;
    right: 0;
    margin: auto;
    min-width: 320px;
    height: 100%;
    transform: translate3d(0%, 0, 0);

    @media (min-width:768px) {
        width: 540px;
    }
}

.modal.right .modal-content {
    height: 100%;
    overflow-y: auto;
}

.cart {
    .modal-header {
        padding-top: 16px;

        @media (min-width:768px) {
            padding-top: 54px;
        }
    }

    .modal-body {
        padding: 1rem 0;

        @media (min-width:768px) {
            padding: 28px 0;
        }

    }

    .cart-item-text {
        background: #F2F2F2;
    }



}

.cart-item {
    .cart-img {
        width: 96px;
        height: 96px;

        @media (min-width:768px) {
            width: 112px;
            height: 112px;
        }
    }

    .subtitle {
        line-height: 14px;

        del {
            font-size: 12px;
            color: #00000062;
            margin-left: 8px;
        }
    }

    .delete {
        top: 8px;
        right: 8px;

        @media (min-width:768px) {
            top: 16px;
            right: 16px;
        }
    }

    .cart-btn-group {
        height: 24px;

        .btn {
            padding: 0;
        }

        input {
            background: #F2F2F2;

        }
    }

}

.cart-summary {
    width: 100%;
    transform: translateX(0px);
    @media (min-width:768px) {
        width: 76%;
        transform: translateX(112px);

    }

    .cart-td {
        padding: 0 0 1rem 0;
    }

    .cart-tr {
        .cart-td {
            padding: 1rem 0 0 0;
        }
    }
}

.checkout-btn {
    padding: 19px 0 18px 0;
    width: 100%;

    // @media (min-width:768px) {
    //     width: 70%;
    // }
}